import React from 'react'
import Edit from '@material-ui/icons/Edit'
import LockOpenIcon from '@mui/icons-material/LockOpen'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined'
// import { useDispatch } from 'react-redux'

const ActionsTemplate = (tooltip, updateCustomerAPI, setOpenPopup, setRecordForEdit, dispatch) => {
  // const dispatch = useDispatch()
  return [
    {
      icon: 'save',
      tooltip: 'Save User',
      onClick: (event, rowData) => setOpenPopup(true),
      isFreeAction: true,
    },
    {
      icon: () => <Edit style={{ color: '#1380C7' }} />,
      tooltip: tooltip,
      onClick: (event, rowData) => {
        setOpenPopup(true)
        setRecordForEdit(rowData)
      },
    },
    (rowData) => ({
      icon: () =>
        rowData.status ? (
          <LockOutlinedIcon style={{ color: '#1380C7' }} />
        ) : (
          <LockOpenIcon style={{ color: '#1380C7' }} />
        ),
      tooltip: rowData.status ? 'Inactivar' : 'Activar',
      onClick: (event, rowData) => {
        rowData.status = !rowData.status
        dispatch(updateCustomerAPI(rowData, rowData.id))
      },
    }),
  ]
}
export default ActionsTemplate
