import React, { useEffect, useState } from 'react'
import { useForm as customForm, Form } from 'src/layout/useForm'
import Controls from 'src/views/utils/controls/Controls'
import {
  FormControl,
  Grid,
  IconButton,
  InputLabel,
  List,
  ListItem,
  ListItemText,
  MenuItem,
  Select,
} from '@mui/material'
import DeleteIcon from '@mui/icons-material/Delete'
import nav from 'src/nav'

const initialFValues = {
  id: 0,
  name: '',
  description: '',
  permissions: [],
}

export const RoleForm = (props) => {
  const { recordForEdit, addOrEdit, cancelForm } = props
  const [menu, setMenu] = useState('')
  const { values, setValues, handleInputChange, resetForm } = customForm(initialFValues)

  useEffect(() => {
    if (recordForEdit != null) {
      console.log(recordForEdit)
      if (recordForEdit.id) setValues({ ...recordForEdit })
    }
  }, [recordForEdit])

  const handleSubmit = (e) => {
    e.preventDefault()
    addOrEdit(values, resetForm)
  }

  const handleReset = () => {
    resetForm()
  }
  const handleChancheCheckBox = (ev, desc) => {
    if (!ev.target.value) {
      const permissions = values.permissions.filter((e) => e.name != ev.target.name)
      setValues({
        ...values,
        permissions: permissions,
      })
    } else {
      setValues({
        ...values,
        permissions: [
          ...values.permissions,
          {
            name: ev.target.name,
            description: desc,
          },
        ],
      })
    }
  }
  const handleCancel = () => {
    cancelForm(resetForm)
  }
  return (
    <Form onSubmit={handleSubmit}>
      <Grid container rowSpacing={0} columnSpacing={2}>
        <Grid item xs={12}>
          <Controls.FormInput
            label="Nombre"
            name="name"
            value={values.name}
            onChange={handleInputChange}
            required
          />
        </Grid>
        <Grid item xs={12}>
          <Controls.FormInput
            label="Descripción"
            name="description"
            multiline
            value={values.description}
            onChange={handleInputChange}
            required
          />
        </Grid>
        <Grid item xs={12}>
          <h4>Permisos</h4>
        </Grid>

        {nav.map((value, index) => (
          <Grid key={value.id} item xs={6} sm={4} md={3}>
            <Controls.FormCheckbox
              key={value.id}
              name={value.values.name}
              label={value.name}
              value={values.permissions.find((e) => e.name === value.values.name) != null}
              onChange={(e) => handleChancheCheckBox(e, value.name)}
            />
          </Grid>
        ))}

        <Grid item xs={12} style={{ textAlign: 'right', paddingTop: 5 }}>
          <Controls.FormButton
            variant="text"
            color="default"
            size="large"
            text="Cancelar"
            onClick={handleCancel}
          />
          <Controls.FormButton
            variant="contained"
            color="primary"
            type="submit"
            size="large"
            text="Guardar"
          />
          {/* <Controls.FormButton
            variant="contained"
            color="default"
            size="large"
            text="Limpiar"
            onClick={handleReset}
          /> */}
        </Grid>
      </Grid>
    </Form>
  )
}
