import axios from 'axios'
import { types } from '../types/types'

export const getAllRoles = () => {
  return async (dispatch) => {
    await axios
      .get(`${process.env.REACT_APP_API_URL_BASE}/api/roles`)
      .then((response) => {
        dispatch(getRoles(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const addRoleAPI = (role) => {
  return async (dispatch) => {
    await axios
      .post(`${process.env.REACT_APP_API_URL_BASE}/api/roles`, role)
      .then((response) => {
        console.log(response)

        dispatch(createRole(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const updateRoleAPI = (role, id) => {
  return async (dispatch) => {
    await axios
      .put(`${process.env.REACT_APP_API_URL_BASE}/api/roles/${id}`, role)
      .then((response) => {
        dispatch(getAllRoles())
        console.log(response)
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const createRole = (role) => ({
  type: types.createRole,
  payload: role,
})

export const updateRole = (role) => ({
  type: types.updateRole,
  payload: role,
})

export const getRoles = (roles) => ({
  type: types.getRoles,
  payload: roles,
})
