import { MTableAction, MTableBodyRow } from 'material-table'
import React from 'react'
import { GridAddButton } from './GridAddButton'

const components = (setOpenPopup, setRecordForEdit) => ({
  Action: (props) => {
    return (
      <>
        {props.action.icon === 'save' ? (
          <GridAddButton onClick={(event) => props.action.onClick(event, props.data)} />
        ) : (
          <MTableAction {...props} />
        )}
      </>
    )
  },
  Row: (props) => {
    return (
      <MTableBodyRow
        {...props}
        onDoubleClick={(e) => {
          setOpenPopup(true)
          setRecordForEdit(props.data)
        }}
      />
    )
  },
})

export default components
