import { Paper, makeStyles } from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { addRoleAPI, updateRoleAPI } from 'src/actions/roles'
import { PopupForm } from 'src/views/utils/controls/PopupForm'
import { RoleForm } from './RoleForm'
import RolesGrid from './RolesGrid'

const useStyles = makeStyles((theme) => ({
  pageContent: {
    margin: theme.spacing(3),
    padding: theme.spacing(3),
  },
  searchInput: {
    width: '75%',
  },
}))

const Roles = () => {
  const dispatch = useDispatch()
  const [openPopup, setOpenPopup] = useState(false)
  const [recordForEdit, setRecordForEdit] = useState(null)
  const classes = useStyles()

  const addOrEdit = (role, resetForm) => {
    if (role.id === 0) {
      delete role.id
      dispatch(addRoleAPI(role))
    } else {
      delete role.tableData
      dispatch(updateRoleAPI(role, role.id))
    }

    resetForm()
    setRecordForEdit(null)
    setOpenPopup(false)
  }

  const cancelForm = (resetForm) => {
    resetForm()
    setRecordForEdit(null)
    setOpenPopup(false)
  }
  return (
    <>
      <Paper>
        <RolesGrid setOpenPopup={setOpenPopup} setRecordForEdit={setRecordForEdit} />
      </Paper>
      <PopupForm
        openPopup={openPopup}
        setOpenPopup={setOpenPopup}
        setRecordForEdit={setRecordForEdit}
        title="Rol"
      >
        <RoleForm recordForEdit={recordForEdit} addOrEdit={addOrEdit} cancelForm={cancelForm} />
      </PopupForm>
    </>
  )
}
export default Roles
